	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Pinko Commies","date":"17 Feb 2008","slug":"/2008-02-17-pinko-commies/","tags":"Y2008"},"html":"<p>Card-carrying Communists infiltrating the inner circle of government... sounds like an accusation of the McCarthy era. But the headline comes from 2008 Germany, and the Communist in question resides in our neighbor community of Buchholz.</p>\n<p>The Left Party was able to enter the state parliament in Niedersachsen election last month with 7% of the vote. Among their newly elected candidates is Christel Wegner from Buchholz, a <a href=\"http://www.dkp-online.de/uz/4005/s0204.htm\">member of the German Communist Party</a> (DKP). Last week she gave a <a href=\"http://www.spiegel.de/international/germany/0,1518,535606,00.html\">television interview</a> in which she defended the East German Stasi (as useful against \"reactionary forces\") and claimed the Berlin Wall was built to keep West Germans out of East Germany.</p>\n<p>The Left Party is the successor (once removed) of East German SED, but says it has <a href=\"http://die-linke.de/presse/presseerklaerungen/detail/zurueck/aktuell/artikel/vorstand-distanziert-sich-in-aller-form/\">\"learned from history\"</a> and is trying to distance itself from Ms. Wegner, especially since the negative publicity could hurt their chances in the Hamburg election next weekend.</p>\n<p>Maybe the Left Party should make sure that the candidates they field have learned from history as well. By the way, their list for Hamburg also includes DKP members.</p>\n<p><strong>Update 18 Feb 2008</strong> Today the Left Party parliamentary faction in Niedersachsen voted to expel Christel from their ranks, and the national Left Party will consider a measure to no longer list \"crossover\" candidates from other leftist parties. Christel remains a faction-less member of the state parliament.</p>"},"next":{"fields":{"title":"Indy Dance","date":"17 Feb 2008","slug":"/2008-02-17-indy-dance/"}},"previous":{"fields":{"title":"Laryngitis","date":"25 Feb 2008","slug":"/2008-02-25-laryngitis/"}}},{"node":{"fields":{"title":"Indy Dance","date":"17 Feb 2008","slug":"/2008-02-17-indy-dance/","tags":"Y2008"},"html":"<p>We're trying out a webcam capture program. Christopher just happened to be playing Indiana Jones...</p>\n<p><object width=\"425\" height=\"350\"><param name=\"FlashVars\" value=\"apiHost=api.sevenload.com\" /><param name=\"AllowScriptAccess\" value=\"always\" /><param name=\"movie\" value=\"http://en.sevenload.com/pl/9DsrgGq/425x350/swf\" /><embed src=\"http://en.sevenload.com/pl/9DsrgGq/425x350/swf\" type=\"application/x-shockwave-flash\" width=\"425\" height=\"350\" allowfullscreen=\"true\" allowscriptaccess=\"always\" flashvars=\"apiHost=api.sevenload.com\"></embed></object><br />Link: <a href=\"http://en.sevenload.com/videos/9DsrgGq/Indy-Dance\">sevenload.com</a></p>"},"next":{"fields":{"title":"Valentine's Day","date":"14 Feb 2008","slug":"/2008-02-14-valentines-day/"}},"previous":{"fields":{"title":"Pinko Commies","date":"17 Feb 2008","slug":"/2008-02-17-pinko-commies/"}}},{"node":{"fields":{"title":"Valentine's Day","date":"14 Feb 2008","slug":"/2008-02-14-valentines-day/","tags":"Y2008"},"html":"<p>The rest of the world may be celebrating Valentine's Day today, but we're so busy with the business (German joke: \"Selbstst&auml;ndig bedeutet immer selbst und st&auml;ndig\") we don't really have time. But considering that we've been together since sometime around Valentine's Day in 1980 (that's so long ago, that Jimmy Carter was President and Helmut Schmidt was Chancellor), a 20 year franchise contract pales in comparison.</p>\n<p>Actually we first met the November before, but the male half was too dense to notice that it could become anything permanent.</p>"},"next":{"fields":{"title":"Forty-Six, and One","date":"09 Feb 2008","slug":"/2008-02-09-forty-six-and-one/"}},"previous":{"fields":{"title":"Indy Dance","date":"17 Feb 2008","slug":"/2008-02-17-indy-dance/"}}}],"pathPrefix":"","first":false,"last":false,"index":83,"pageCount":1101}}

